.care-jewellery {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0 0 30px;
  padding: 0;

  @media (max-width: $max-tablet) {
    margin: 0 0 50px;
  }

  @media (max-width: 640px) {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 0 40px;
  }
}

.care-jewellery__picture {
  background: url("../img/desktop/main/blog/care-jewellery.jpg") no-repeat;
  background-size: cover;
  background-color: $basic-brown;
  min-width: 285px;
  min-height: 300px;

  @media (max-width: $max-tablet) {
    background: url("../img/tablet/main/blog/care-jewellery.jpg") no-repeat;
    background-size: cover;
    min-width: 339px;
    min-height: 300px;
  }

  @media (max-width: 738px) {
    min-width: calc(100vw - 400px);
  }

  @media (max-width: 640px) {
    background: url("../img/mobile/main/blog/care-jewellery.jpg") no-repeat;
    background-size: cover;
    min-height: 256px;
    min-width: 100%;
  }
}

.care-jewellery__text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  background-color: $basic-beige;
  min-width: 285px;
  min-height: 300px;
  max-width: 285px;
  margin: 0;
  padding: 66px 60px 20px;

  @media (max-width: $max-tablet) {
    min-width: 339px;
    min-height: 300px;
  }

  @media (max-width: 738px) {
    max-width: 258px;
    min-width: 100%;
  }

  @media (max-width: 640px) {
    min-height: 256px;
    padding: 44px 60px 20px;
  }
}

.care-jewellery p {
  transition: 0.2s;

  &:hover {
    transform: scale(1.2);
    transition: 0.2s;
  }

  &:first-of-type {
    font-size: 14px;
    line-height: 18px;
    color: $basic-grey;
    margin: 0 0 14px;
    padding: 0;
  }

  &:last-of-type {
    border-bottom: 1px solid $basic-brown;
    margin: 0;
    padding: 0 0 3px;
  }
}

.care-jewellery a {
  color: $basic-dark;

  &:active {
    color: $basic-brown;
  }
}

.care-jewellery h3 {
  @include cormorantgaramond-semibold();
  max-width: 180px;
  text-align: center;
  margin: 0 0 38px;
  padding: 0;
}
